#calendar-page {
	--calendar-bg-color: var(--primary);

	width: 100%;
	height: 100%;
	position: absolute;
	left: 0;
	top: 0;
	overflow: hidden;
	background: var(--calendar-bg-color);
	transition: background-color 0.5s cubic-bezier(0.215, 0.61, 0.355, 1);

	#calendar-blur {
		padding: 0.5rem;
		width: 100%;
		height: 100%;
		background: var(--calendar-bg-color);
		transition: background-color 0.5s cubic-bezier(0.215, 0.61, 0.355, 1);
		z-index: 5;
		font-weight: bolder;

		svg {
			position: relative;
			width: 100%;
			height: inherit;
			z-index: 100;

			text {
				text-anchor: left;
				fill: white;
			}

			#title {
				letter-spacing: -2px;
				font-size: 6em;
				font-weight: 800;
				font-family: Arial, Helvetica, sans-serif;
			}

			#base {
				fill: #000;
				mask: linear-gradient(#fff, #fff), url(#mask);
				mask-composite: intersect;
			}
		}
	}

	.item-banner {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 110%;
		background-position: 0, right;
		background-blend-mode: overlay;
		background-size: 120% auto;
		mask-composite: normal, add;
		mask-size: 100% auto;
		mask-repeat: no-repeat;
		mask-position: 100% 90%;
		z-index: 2;

		&:not(.cát) {
			opacity: 0;
		}

		&.reversed {
			-moz-transform: scaleX(-1);
			-o-transform: scaleX(-1);
			-webkit-transform: scaleX(-1);
			transform: scaleX(-1);
		}

		&:not(.solo) {
			mask-size: 70% auto, auto;
		}
	}

	.item {
		--highlight-color: #fff1;
		--highlight-border-color: #fff;

		&:not(.solo) {
			--highlight-color: #fa03;
			--highlight-border-color: var(--secondary);
		}

		&.highlighted::before {
			content: "";
			position: absolute;
			top: 0;
			left: 0;
			width: 100%;
			height: 100%;
			z-index: 5;
			animation: ramo-highlight-spawn 2s cubic-bezier(0.19, 1, 0.22, 1);
			mix-blend-mode: difference;

			background: var(--highlight-color);
			box-shadow: inset 0px 0px 0px 3px var(--highlight-border-color);
			animation: ramo-highlight-idle 0.4s ease infinite alternate;
		}

		.item-title,
		.item-sigla,
		.item-tipo {
			z-index: 2;
			position: relative;
			text-shadow: 0 0 1px black, 1px 1px 0 black, 2px 2px 0 black, 2px 2px 3px black, 0px 0px 2px black, 0px 0px 5px black;
		}

		.item-sigla,
		.item-tipo {
			color: white;
		}

		.item-tipo {
			filter: brightness(2);
		}

		--tipo-blend-mode: normal;
		--mask-gradient-normal: linear-gradient(to right, white, white);
		--mask-gradient-right: linear-gradient(to right, transparent 50%, white 50%);
		--mask-gradient-left: linear-gradient(to left, transparent 50%, white 50%);

		&.ayu {
			.item-tipo {
				color: var(--primary);
			}

			&:not(.solo) {
				&:not(.right)::after {
					mask-image: var(--mask-gradient-left);
				}
				&::after {
					mask-image: var(--mask-gradient-right);
				}
			}

			&::after {
				content: "";
				position: absolute;
				left: 0;
				top: 0;
				height: 100%;
				width: 100%;
				background: var(--primary);
				mix-blend-mode: var(--tipo-blend-mode);
				z-index: 1;
				opacity: 0.2;
				mask-image: var(--mask-gradient-normal);
			}
		}

		&.prá {
			.item-tipo {
				color: var(--terciary);
			}
			&:not(.solo) {
				&:not(.right)::after {
					mask-image: var(--mask-gradient-left);
				}
				&::after {
					mask-image: var(--mask-gradient-right);
				}
			}

			&::after {
				content: "";
				position: absolute;
				left: 0;
				top: 0;
				height: 100%;
				width: 100%;
				background: var(--terciary);
				mix-blend-mode: var(--tipo-blend-mode);
				z-index: 1;
				opacity: 0.2;
				mask-image: var(--mask-gradient-normal);
			}
		}
		&.lab {
			.item-tipo {
				color: var(--secondary);
			}

			&:not(.solo) {
				&:not(.right)::after {
					mask-image: var(--mask-gradient-left);
				}
				&::after {
					mask-image: var(--mask-gradient-right);
				}
			}

			&::after {
				content: "";
				position: absolute;
				left: 0;
				top: 0;
				height: 100%;
				width: 100%;
				background: var(--secondary);
				mix-blend-mode: var(--tipo-blend-mode);
				z-index: 1;
				opacity: 0.2;
				mask-image: var(--mask-gradient-normal);
			}
		}
		&.otr {
			.item-tipo {
				color: var(--quaternary);
			}

			&:not(.solo) {
				&:not(.right)::after {
					mask-image: var(--mask-gradient-left);
				}
				&::after {
					mask-image: var(--mask-gradient-right);
				}
			}

			&::after {
				content: "";
				position: absolute;
				left: 0;
				top: 0;
				height: 100%;
				width: 100%;
				background: var(--quaternary);
				mix-blend-mode: var(--tipo-blend-mode);
				z-index: 1;
				opacity: 0.2;
				mask-image: var(--mask-gradient-normal);
			}
		}
	}

	.calendar-bloque {
		font-family: mono;
	}
}

@keyframes ramo-highlight-spawn {
	from {
		opacity: 0;
		box-shadow: inset 0px 0px 0px 0px var(--highlight-border-color);
	}
}

@keyframes ramo-highlight-idle {
	from {
		opacity: 0.7;
		box-shadow: inset 0px 0px 0px 1px var(--highlight-border-color);
	}
	to {
		opacity: 1;
		box-shadow: inset 0px 0px 0px 3px var(--highlight-border-color);
	}
}
