#usm-loader-ripples {
	position: relative;
	aspect-ratio: 1;
	width: 6rem;
}

.usm-loader-ripple {
	position: absolute;
	top: 0;
	left: 0;
	margin: 0;
	width: 100%;
	height: 100%;
	background-color: var(--primary);
	border-radius: 50%;
	mask-image: radial-gradient(transparent, transparent 65%, white 0, white);
	animation: 1s UsmRippleAnimation linear infinite;
	opacity: 0;
	scale: 0;

	&.delayed {
		animation-delay: 0.5s;
	}
}

@keyframes UsmRippleAnimation {
	from {
		scale: 0;
		opacity: 1;
	}
	to {
		scale: 1;
		opacity: 0;
	}
}
