body .glass-bg {
	box-shadow: 0 2px 12px 0 rgba(0, 0, 0, 0.5);
	border: 1px solid rgba(255, 255, 255, 0.25);

	&::before {
		content: "";
		width: 100%;
		height: 100%;
		top: 0;
		left: 0;
		position: absolute;
		background: url(./static/noise1.png);
		background-color: #0008;
		background-blend-mode: overlay;
		opacity: 0.12;
	}
}

body:not(.optimized) .glass-bg {
	--glass-bg-blur: 0.3rem;
	backdrop-filter: blur(var(--glass-bg-blur));
	-webkit-backdrop-filter: blur(var(--glass-bg-blur));
}

.times-new-roman {
	font-family: "Times New Roman", Times, serif;
}

.warning-bg {
	--stripe-color: var(--secondary);
	--stripe-size: 32px;

	&::before {
		content: "";
		position: absolute;
		left: 0;
		top: 0;
		width: 100%;
		height: 100%;
		opacity: 0.2;
		background-image: repeating-linear-gradient(45deg, transparent, transparent 32px, var(--stripe-color) var(--stripe-size), var(--stripe-color) calc(var(--stripe-size) * 2));
	}
}
