.creator-card {
	--space: 20%;
	--gradient-x: 0%;
	--particles-position: 0 0;
	--shine-size: 35rem;

	width: 15rem;
	height: auto;

	transition: transform 100ms ease, scale 700ms cubic-bezier(0.19, 1, 0.22, 1), opacity 0.5s cubic-bezier(0.445, 0.05, 0.55, 0.95);
	transform-style: preserve-3d;
	will-change: transform;
	box-shadow: 0 0 1rem black;
	overflow: hidden;
	border: 1px solid #fffa;
	scale: 0.85;
	opacity: 1;

	@starting-style {
		scale: 0.5;
		opacity: 0;
	}

	&::after {
		content: "";
		position: absolute;
		left: 0;
		top: 0;
		width: 100%;
		height: 100%;
		background-blend-mode: overlay, hue, hard-light, multiply, overlay;
		background: url(../../../static/holo-texture.png), url(../../../static/holo-color.png), repeating-linear-gradient(45deg, #0e152e 0%, hsl(180, 10%, 60%) 3.8%, hsl(180, 29%, 66%) 4.5%, hsl(180, 10%, 60%) 5.2%, #0e152e 10%, #0e152e 12%),
			radial-gradient(farthest-corner circle at 50% 50%, rgba(0, 0, 0, 0.2) 12%, rgba(0, 0, 0, 0.25) 20%, rgba(0, 0, 0, 0.5) 120%), url(../../../static/stars_gif.gif);
		background-size: 100%, auto, 2000% auto, auto, 70%;
		background-position: 0, center center, var(--gradient-x), 0, var(--particles-position);
		mix-blend-mode: color-dodge;
		filter: saturate(1.5) brightness(1.2) contrast(1);
	}

	.creator-card-shine {
		z-index: 100;
		background: #fff8;
		position: absolute;
		left: 0;
		top: 0;
		height: 0;
		width: var(--shine-size);
		height: auto;
		aspect-ratio: 1;
		transform-origin: center;
		translate: -50% -50%;
		backdrop-filter: brightness(1.5) contrast(1.2) saturate(1.2);
		mask-image: radial-gradient(farthest-corner circle at 50% 50%, #fff, transparent 50%);
		mix-blend-mode: lighten;
	}
}

.creator-scrolling-text {
	display: inline-block;
	white-space: nowrap;
	font-weight: bolder;
	font-family: Arial, Helvetica, sans-serif;
	font-size: 1rem;
	pointer-events: none;
	animation: 5s scrollText infinite linear;
	width: fit-content;
	color: white;
	z-index: 1000;
}

@keyframes scrollText {
	from {
		transform: translateX(-20%);
	}

	to {
		transform: translateX(-40%);
	}
}
