body #home-page {
	top: 0;
	left: 0;
	position: absolute;
	width: 100%;
	height: 100%;
	z-index: 100;
	background-color: #000b;
	transition: 1s backdrop-filter ease-out, 2s background-color cubic-bezier(0.19, 1, 0.22, 1), opacity 0.5s ease;
	opacity: 1;

	@starting-style {
		background-color: #0000;
		opacity: 0;
	}

	&::before {
		position: absolute;
		content: "";
		width: 100%;
		height: 100%;
		top: 0;
		left: 0;
		background: var(--primary);
		mix-blend-mode: multiply;
		opacity: 0.3;
	}
}

body:not(.optimized) #home-page {
	backdrop-filter: blur(1rem);
	background-color: #0007 !important;

	@starting-style {
		backdrop-filter: blur(0);
	}
}

#home-main-box {
	position: absolute;
	width: 50%;
	left: 50%;
	top: 50%;
	translate: -50% -50%;
	padding: 1.5rem;
	border-radius: 0.5rem;
	overflow: hidden;
	animation: homeBoxFadeIn 1s cubic-bezier(0.165, 0.84, 0.44, 1);
}

#home-main-box-bg {
	position: absolute;
	top: 0;
	left: 0;
	height: 70%;
	width: 100%;
	margin: 0;
	mask-image: linear-gradient(white, transparent);
}

.home-button {
	position: relative;

	.text {
		transition: all 0.2s ease;
		opacity: 0;
	}

	&:not(:disabled):hover {
		background: #0005;
		&:active {
			background: #fff2;
		}

		img {
			scale: 1.1;
		}

		.text {
			opacity: 1;
		}
	}

	img {
		transition: 0.4s cubic-bezier(0.2, 1, 0.2, 1);
		transform-origin: top;
		position: absolute;
		left: 0;
		bottom: -30%;
		height: auto;
		pointer-events: none;
	}
}

.home-button-bg {
	width: 100%;
	height: 100%;
	position: absolute;
	left: 0;
	top: 0;
	background: #fff4;
	mask-image: linear-gradient(to top, transparent 20%, white);
}

@keyframes titleFadeIn {
	0% {
		opacity: 0;
		transform: translateX(-10%);
	}

	100% {
		opacity: 1;
	}
}
