#sede-selector {
	transition: border 0.5s cubic-bezier(0.19, 1, 0.22, 1);

	&.error {
		animation: 0.5s sedeSelectorError ease infinite alternate;
		border: 2px solid red;
	}
}

@keyframes sedeSelectorError {
	100% {
		border: 2px solid white;
	}
}

#sede-locator-bg {
	position: absolute;
	left: 0;
	top: 0;
	pointer-events: none;
	width: 100%;
	height: 100%;

	img,
	.no-sede {
		position: relative;
		top: 0;
		left: 0;
		mask-image: linear-gradient(to right, #000f 25%, #0000 65%);
		animation: imageChange 1s cubic-bezier(0.165, 0.84, 0.44, 1);
	}

	img {
		height: auto;
		width: 100%;
	}

	.no-sede {
		width: 100%;
		height: 100%;
	}

	.no-sede:before {
		content: " ";
		position: absolute;
		left: 0;
		top: 0;
		width: 100%;
		height: 100%;
		opacity: 0.5;
		background-image: url("../../../static/error-lines.png");
		background-size: 4rem auto;
		background-position: 0 50%;
		background-repeat: repeat-x;
		animation: errorBackgroundScroll 0.5s infinite linear;
	}
}

#sede-locator-bg-overlay {
	position: absolute;
	right: 0;
	top: 0;
	width: 100%;
	height: 100%;
	animation: jornadaChange 0.7s cubic-bezier(0.19, 1, 0.22, 1);

	&.jornada-vespertina {
		background: url("../../../static/stars_seamless_patt.jpg");
		background-position: right top;
		background-size: 100% auto;
	}

	&.jornada-diurna {
		background-image: url("../../../static/sun_banner.jpg");
		background-position: right top;
		background-size: 100% auto;
	}
}

@keyframes imageChange {
	0% {
		scale: 1.1;
	}
}

@keyframes jornadaChange {
	0% {
		background-position: 2rem 0;
		filter: blur(0.5rem);
	}
}

@keyframes errorBackgroundScroll {
	100% {
		background-position-x: 4rem;
	}
}
